import React, { Component } from 'react';

import WSPopulationAndCellOpsComponent from '../components/Reports/WSPopulationAndCellOps'


const Button = ({tab, setTab}) => (
  <button className='btn bg-mainDark-dark mx-1 my-1 font-12 text-wrap' onClick={() => setTab(tab)}>
    {tab.title}
  </button>
)

class WSMainPage extends Component {
   constructor(props) {
    super(props);
  }
  
  render() {
    const { ws_number, setTab, tabs } = this.props

    return (
        <div className='container-fluid'>
          {/* <h1> {ws_number}</h1> */}
          <p className='my-0 font-15'></p>
          <div className='d-flex justify-content-around table-responsive'>
            <Button tab={{name: 'searchSowTab', title: '   '}} setTab={setTab}/>
            <Button tab={{name: 'comingSowsTab', title: ' '}} setTab={setTab}/>
            <Button tab={{name: 'farrowTab', title: ''}} setTab={setTab}/>
            <Button tab={{name: 'nurseSowTab', title: ''}} setTab={setTab}/>
            <Button tab={{name: 'sowCullingTab', title: '' }} setTab={setTab}/>
            <Button tab={{name: 'sowInnerTransferTab', title: '    '}} 
              setTab={setTab}/>
            <Button tab={{name: 'sowTransferToWsTab', title: '  1, 3'}} setTab={setTab}/>
            <Button tab={{name: 'sowAndPigletsTransferTab',
              title: '       '}} setTab={setTab}/>
          </div>

          <p className='my-0 font-15'></p>
          <div className='d-flex justify-content-start'>
            <Button tab={{name: 'returnPigletsTab', title: ' '}} setTab={setTab}/>
            <Button tab={{name: 'weaningPigletsTab', title: ' '}} setTab={setTab}/>
            <Button tab={{name: 'createGiltTab', title: ''}} setTab={setTab}/>
            <Button tab={{name: 'pigletsCullingTab', title: ' '}} setTab={setTab}/>
            <Button tab={{name: 'pigletsInnerTransferTab', title: '    '}} 
              setTab={setTab}/>
            <Button tab={{name: 'pigletsRecountTab', title: ''}} setTab={setTab}/>
          </div>

          <p className='my-0 font-15'></p>
          <div className=''>
            <Button tab={{name: 'operationsTab', title: ''}} setTab={setTab}/>
            <Button tab={{name: 'reportsTab', title: ''}} setTab={setTab}/>
          </div>

          <WSPopulationAndCellOpsComponent 
              getWsPopulation={this.props.getWsPopulation}
              populationData={this.props.populationData}
            />

          {/* {tabs.map(tab =>
            <button className='btn bg-mainDark-dark mx-2 my-2 font-20' onClick={() => setTab(tab)}>
              {tab.title}
            </button>
            )} */}
        </div>
    )
  }
}

export default WSMainPage